export default {
	data() {
		return {
			isLoaing: true,
			orders: null,
			isNull: false
		}
	},
	onShow() {
		this.isLoaing = true
		this.getReturnOrders()
	},
	methods: {
		toReturnDetail(index) {
			uni.setStorageSync('returnDetail',this.orders[index])
			uni.navigateTo({
				url: '../returnDetail/returnDetail'
			})
		},
		sureGet(index) {
			this.setheader()
			uni.request({
				url: this.websiteUrl + "/api/order/afterGoodsAccomplish",
				data: {
					token: uni.getStorageSync('token'),
					id: uni.getStorageSync('returnDetail').id,
					shop_id: uni.getStorageSync('shop_id')
				},
				header: this.header,
				method: 'POST',
				success: (res) => {
					console.log(res.data)
					if(res.data.status==0){
						uni.showToast({
							title:'退换货完成',
							icon:'success'
						})
						this.getReturnOrders()
						return
					}
					this.showToast(res.data.msg)
				}
			})
		},
		getReturnOrders() {
			this.setheader()
			uni.request({
				url: this.websiteUrl + "/api/Order/returnGoodsList",
				data: {
					token: uni.getStorageSync('token'),
					shop_id: uni.getStorageSync('shop_id')
				},
				header: this.header,
				success: (res) => {
					console.log(res.data)
					if (res.data.status == 0) {
						this.isLoaing = false
						this.orders = res.data.data.data
						if (this.orders.length == 0) {
							this.isNull = true
							return
						}
						for (var i = 0; i < this.orders.length; i++) {
							this.orders[i].final_price = this.orders[i].price
						}
					}
				}
			})
		}
	}
}
